@import "../../../common/styles/variables";

.skillBlockWrapper {
  flex: 0 0 100%;
  max-width: 100%;
  position: relative;
  width: 100%;
  min-height: 1px;
  padding: 15px;
  @media screen and (min-width: $tabletop) {
    flex: 0 0 50%;
    max-width: 50%;
  }
  @media screen and (min-width: $smallDesktop) {
    flex: 0 0 33.3333%;
    max-width: 33.3333%;
  }

  .skillBlock {
    padding: 35px 8%;
    text-align: center;
    position: relative;
    z-index: 1;
    background: $primaryColor;
    margin: 15px 0;
    border-radius: 5px;
    box-shadow: 0px 1px 27px -3px RGB(0 0 0 / 20%);
    @media screen and (max-width: $smallDesktop-1px) {
      margin: 15px 0;
    }

    .icon {
      width: 100px;
      height: 100px;
      display: inline-block;
      vertical-align: top;
      border-radius: 50%;
      line-height: 100px;
      font-size: 40px;
      color: $primaryColor;
      margin-bottom: 30px;
      transition: ease all 0.3s;
      background: $accentColor;
    }

    &:hover .icon {
      background: #333;
    }

    .content {
      & h5 {
        font-size: 16px;
        color: $fontSecondColor;
        margin-bottom: 15px;
        font-weight: 700;
        font-family: inherit;
        line-height: 1.2;
        margin-top: 0;
      }

      .skillDescription {
        margin: 0 0 25px;
      }
    }
  }
}





