@import "./variables";

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  @media screen and (min-width: $mobile) {
    max-width: 540px;
  }
  @media screen and (min-width: $tabletop) {
    max-width: 720px;
  }
  @media screen and (min-width: $smallDesktop) {
    max-width: 960px;
  }
  @media screen and (min-width: $desktop) {
    max-width: 1140px;
  }
}






